	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 20 – La trahison","texte":{"childMarkdownRemark":{"html":"<p>La soirée de la journée même, je me réveille petit à petit attaché de la tête au pied avec un bandeau sur les yeux et un baillons dans la bouche. Qu’est-ce qui se passe? Pourquoi suis-je attaché de la sorte et par qui? J’entends des rires qui proviennent de devant moi, j’active ma vision thermique. J’essaie de bouger, impossible, je m’agite comme un saucisson.</p>\n<p>Inconnu: Hé oh du calme Wamien. On ne va pas te manger.<br>\nSaeko: Il ne sait pas où il est c’est normal, Drake.</p>\n<p>Putain Saeko, elle est derrière ça? Pourquoi elle fait ça? Elle m’a emmené au Black Eagles, pourquoi?</p>\n<p>Drake: Hm, je savais que tu ferais une bonne Black Eagles.<br>\nSaeko: Il ne suffisait pas juste de l’emmener, non? Vous allez lui faire quoi après?<br>\nDrake: Prendre possession de ses pouvoirs et de sa volonté.</p>\n<p>Volonté? Pourquoi prendre ma volonté?</p>\n<p>Saeko: De quoi prendre sa volonté?<br>\nDrake: Aller Ray vient par ici.</p>\n<p>J’entends des bruits de pas qui s’approchent.</p>\n<p>Inconnu: Je suis prêt Drake, vous pouvez lui retirer le bandage des yeux.</p>\n<p>Je sens des mains qui se glissent derrière ma tête et défaire le nœud du bandage. Je ferme les yeux à cause de l’intense lumière du lieu et les ouvres progressivement par la suite. Saeko est sur la droite de Drake et lui tient la main en souriant. La traitresse… Je regarde Drake, un peu plus petit que moi, mais il affiche un sourire victorieux en face de moi. Ray se place devant moi et place ses mains sur mes tympans, il commence à parler mais je ne comprends rien. Je le dévisage et observe ce qu’il fait. Je le sens, il essaie d’entrer dans ma tête pour me contrôler. Je ferme les yeux et le laisse entrer en partie dans ma tête. Il arrête de parler un instant et retire ses mains.</p>\n<p>Ray: Voilà, il est sous mon contrôle, on ne risque plus rien.</p>\n<p>J’ouvre les yeux, Drake affiche un sourire comme s’il venait d’obtenir un nouveau jouet.</p>\n<p>Drake: On peut le détacher alors.</p>\n<p>Ray commence à me détacher, il veut que je sorte des cordages et que j’aille m’asseoir à une chaise. Je le fais sans discuter, toujours en faisant semblant de rien.</p>\n<p>Saeko: C’est aussi facile que ça son truc de contrôle mental, wow.<br>\nDrake: Mis à part toi, il n’y a que Ray et Kellan qui partage mes idées, tous les autres sont sous le contrôle de Ray.<br>\nRay: Et je peux contrôler autant de personnes que je le souhaite.<br>\nSaeko: Sympa~<br>\nDrake: Ouais et puis bientôt, on va dévaliser la Banque Royale, il nous fallait quelqu’un pour pouvoir déverrouiller le coffre-fort.<br>\nSaeko: Pourquoi autant d’argent?<br>\nDrake: Recruter des gens, des mercenaires pour faire les tâches ingrates.<br>\nSaeko: C’est quand même plus plaisant de casser la gueule à deux ou trois personnes.</p>\n<p>Un cri se fait entendre de l’autre côté du mur et quelqu’un frappe dedans. </p>\n<p>Drake: Qu’est-ce qui se passe?</p>\n<p>Au second coup, le mur vole aux éclats et je peux reconnaître la silhouette de Tim qui respire rapidement. D’autres silhouettes un peu plus petites entrent dans la pièce malgré la poussière qui s’y trouve.</p>\n<p>Aussitôt Gaïa et Hiro entrent dans la pièce via une porte à l’autre bout. Hiro invoque deux épées et Gaïa commence à faire pousser ses lianes.</p>\n<p>Drake: Non! Laissez Wamien se charger de ses amis. Ça peut être amusant.</p>\n<p>Ray veut que je me lève et que j’aille frapper Tim. Je me lève et regarde Tim, me place en position de combat. Lorsqu’il me dit de courir le frapper, je sors le taser de mon avant-bras et électrocute Ray qui relâche tous ceux qui étaient sous son contrôle. Drake ne comprend pas, il a la bouche entre-ouverte et recule nerveux. Gaïa et Hiro tombent dans les pommes un peu après Ray.</p>\n<p>Saeko: Enculé! T’as fait semblant!</p>\n<p>Elle était rouge de colère et me pointait du doigt.</p>\n<p>Wamien: Alexis! Empêche Drake et Saeko de bouger!<br>\nAlexis: Tout de suite!</p>\n<p>Il dirige ses mains vers les deux cibles et les surélèves. Drake commence à bouger de plus en plus vite pour essayer de se dégager de l’emprise d’Alexis.</p>\n<p>Wamien: Place-le au-dessus du vide, on va voir s’il continue de faire le malin.</p>\n<p>Alexis s’exécute et Drake se retrouve au-dessus du vide. Il arrête de bouger net. Tim et Kellan ont commencé à se battre, c’est un combat de titan, la pièce vibre à chaque échange de coups. Louann envoie de l’eau sur Kellan et ordonne ensuite en Tim de reculer, ce qu’il fait en comprenant l’idée. Bailey était à côté de Louann et envoie une puissante décharge électrique à Kellan qui s’écroule lourdement sur le sol de l’appartement.</p>\n<p>Drake tombe ayant réussi à sortir de l’emprise d’Alexis.</p>\n<p>Saeko: Drake non!!</p>\n<p>Alexis et moi tournons la tête vers la fenêtre. Alexis semble s’être prit un coup en une fraction de seconde revole dans un mur pour finir au sol. Alexis inconscient, Saeko libre de ses mouvements. Drake est proche d’où Alexis était Saeko vient se placer à côté de lui.</p>\n<p>Saeko: On va leur faire leur fête.</p>\n<p>Elle se craque les doigts et s’approche vers moi. Clara va près d’Alexis pour le soigner. Je vois Kémyann qui sort du sol derrière Drake, lui attrape les pieds et l’enfonce dans le plancher comme du sable mouvant le ferait. Elle arrête une fois qu’il est rendu aux épaules et aux poignets.</p>\n<p>Drake: Wow!</p>\n<p>Saeko essaie de me frapper, je la bloque, son coup était vraiment fort que j’avais senti quelque chose se briser dans mon bras. Avec mon autre avant-bras et lui frappe dans le ventre avec mon poing. Elle recule un peu et me regarde. Je savais qu’elle était distante, mais au point d’être une traitresse, ça je n’en reviens pas encore. Elle recommence à venir vers moi et me fait une balayette, je tombe sur le dos et elle place son pied sur ma gorge pour appuyer de plus en plus fort. Je n’arrive plus à respirer et je place mes mains sur son pied pour essayer de le retirer.</p>\n<p>Saeko: Arrête donc ce n’est pas parce que tu as survécu.</p>\n<p>Tim lui fonce dessus et la plaque contre le mur. Saeko se prend le choc en poussant un gémissement de douleur. Clara vient près de moi.</p>\n<p>Clara: Doucement, elle t’a brisé quelque chose dans le bras.</p>\n<p>Je tousse pour essayer de reprendre une respiration normale.</p>\n<p>Wamien: Je sais Clara.</p>\n<p>Drake réussit à briser le sol autour de lui et à remonter, Tim se prend un coup et puis Saeko, Kellan et Ray disparaissent.</p>\n<p>Wamien: Ils… ils sont partis?<br>\nClara: Oui, mais il a laissé derrière lui tous ceux que Ray contrôlait.<br>\nWamien: On retourne à la campagne avec eux.</p>"}},"chapitreAvant":"chapitre-19-le-perfectionnement","chapitreApres":"chapitre-21-les-reveils","nomRoman":"le-premier-cyborg-tome-1","slug":"chapitre-20-la-trahison","codeChapitre":"H0001C021","equivalentUrl":"/stories/chapter/chapter-20-betrayal"}},"pathContext":{"slug":"chapitre-20-la-trahison"}}

